.nav-wrapper {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
  z-index: 10;
}

.nav {
  display: flex;
  max-width: 1140px;
  margin: 0 auto;
  justify-content: space-between;
  align-items: center;
}

@media (max-width: 75em) {
  .nav {
    padding: 0 0 2rem 1rem;
    border-bottom: 3px solid #58a7b4;
  }
}

.nav__list {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 80%;
}

@media (max-width: 75em) {
  .nav__list {
    width: 80%;
  }
}

@media (max-width: 57.8125em) {
  .nav__list {
    display: none;
  }
}

.nav__item {
  list-style: none;
  margin-right: 40px;
}

.nav__link {
  text-decoration: none;
  /* text-transform: uppercase; */
  letter-spacing: 1px;
  color: #fff;
  font-size: 1.6rem;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}

.nav__link:hover {
  color: #a0feea;
}

.nav__checkbox {
  display: none;
}

.nav__checkbox:checked ~ .nav-small {
  width: 100%;
  opacity: 1;
}

.nav__toggle-btn {
  background-color: transparent;
  height: 7rem;
  width: 7rem;
  position: fixed;
  top: 1rem;
  right: 6rem;
  z-index: 2000;
  box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.1);
  text-align: center;
  cursor: pointer;
  display: none;
  border-radius: 50%;
}

@media (max-width: 57.8125em) {
  .nav__toggle-btn {
    display: flex;
    top: 2rem;
    justify-content: flex-end;
    align-items: center;
    flex-direction: column;
  }
}

.nav__icon {
  position: relative;
}

.nav__icon,
.nav__icon::before,
.nav__icon::after {
  width: 5rem;
  height: 5px;
  background-color: #fff;
  display: inline-block;
}

.nav-small {
  position: fixed;
  top: 0;
  left: 0;
  width: 0%;
  height: 100%;
  background: #000;
  z-index: 1000;
  opacity: 0;
  -webkit-transform-origin: right;
  transform-origin: right;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  text-decoration: none;
  text-transform: uppercase;
  color: #fff;
  font-size: 3rem;
}

.nav-small__list {
  list-style: none;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.nav-small__item:not(:last-child) {
  margin-bottom: 2rem;
}

.nav-small__link {
  text-decoration: none;
  text-transform: uppercase;
  color: #fff;
  font-size: 3rem;
}

.nav__selection,
.nav-small__selection {
  min-width: 10rem;
  font-size: 1.6rem;
  cursor: pointer;
  border-radius: 3px;
}

