.homepage-solution {
  position: relative;
  font-family: 'Montserrat', sans-serif;
}

.homepage-solution--even {
  color: #fff;
}

.homepage-solution--odd {
  color: #000;
  min-height: 600px;
}

.homepage-solution__main {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.homepage-solution__heading {
  font-weight: bolder;
}

.homepage-solution__heading--even {
  color: #fff;
}

.homepage-solution__heading--odd {
  color: #06658d;
}

.homepage-solution__btn-group .btn--green {
  margin-right: 2rem;
  z-index: 1;
}

.homepage-solution__btn-group {
  z-index: 1;
}

.homepage-solution__description {
  font-size: 20px;
}

